import React, { memo } from 'react';
import ControllButton from 'core/src/ControlBar/ContollButton';

import { IconPause, IconPlay } from 'core/src/icons';

interface PlayButtonProps {
  /** Сейчас файл проигрывается? */
  isPlaying: boolean;

  /** Переключить воспроизведение Старт <> Пауза */
  togglePlay: VoidFunction;

  /** ID для тестов */
  dataTestId?: string;
}

/** Отрисовка кнопки управления воспроизведением */
function PlayButton({
  isPlaying,
  togglePlay,
  dataTestId
}: PlayButtonProps): JSX.Element {
  const icon = isPlaying
    ? <IconPause dataTestId="inputFile_uploadedScreen_play_pause" />
    : <IconPlay dataTestId="inputFile_uploadedScreen_play_run" />;

  return (
    <ControllButton
      onClick={ togglePlay }
      dataTestId={ dataTestId }
    >
      { icon }
    </ControllButton>
  );
}

const MemoPlayButton = memo(PlayButton);
MemoPlayButton.displayName = 'PlayButton';

export default MemoPlayButton;
